* {
  box-sizing: border-box;
}

h1, h2, h3, ul, li, p {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  display: block;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  align-items: center;
  padding-left: 50px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

#search-box {
  font: inherit;
  max-width: 320px;
  border-radius: 6px;
  margin-top: 16px;
  margin-bottom: 16px;
  padding: 6px;
  font-size: 22px;
}

.country-list {
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  transform: translateX(101%);
}

.country-list.translate {
  transform: translateX(0);
}

.country-list__item {
  margin-top: 20px;
}

.country-list__link {
  color: #212121;
  align-items: center;
  font-size: 25px;
  font-weight: 500;
  display: flex;
}

.country-list__name {
  margin-left: 20px;
}

.country-info {
  opacity: 0;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1);
}

.country-info.opacity {
  opacity: 1;
}

.country-info__wrap {
  align-items: center;
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
}

.country-info__title {
  margin-left: 20px;
  font-size: 35px;
  font-weight: 700;
}

.country-info__subtitle {
  font-size: 20px;
  font-weight: 500;
}

.country-info__text {
  margin-left: 5px;
  font-size: 17px;
  font-weight: 500;
}

.weather-box {
  width: 150px;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin-left: 50px;
  display: flex;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16), 0 -1px 1px rgba(0, 0, 0, .12), 0 -4px 4px rgba(0, 0, 0, .06), -1px -4px 6px rgba(0, 0, 0, .16);
}

.weather__title {
  width: 120px;
  text-align: center;
  border-bottom: 2px solid #212121;
}

.temp-box, .desc-box {
  width: 120px;
  border-top: 2px solid #212121;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.weather__icon {
  margin-left: auto;
  margin-right: auto;
}

.wind-box {
  width: 120px;
  border-top: 2px solid #212121;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.map-button {
  width: 120px;
  height: 30px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-top: 2px solid #212121;
}

